body {
  background: #2f435f;
}

.login {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login__container {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #ffffff;
  padding: 30px;
  width: 350px;
}
.login__textBox {
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
  outline: none;
}
.login__btn {
  padding: 10px;
  font-size: 18px;
  margin-bottom: 10px;
  border: none;
  color: white;
  background-color: black;
  cursor: pointer;
  border-radius: 4px;
}
.login__google {
  background-color: #4285f4;
  cursor: pointer;
  border-radius: 4px;
}
.login div {
  margin-top: 7px;
}



